import React, { useEffect } from 'react'
import { auth, provider } from '../firebase'
import styled from 'styled-components'
import { selectUserName, selectUserPhoto, setUserLogin, setSignOut } from '../features/user/userSlice'
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router';
import logo from '../images/logo.svg'
import Img1 from '../images/home-icon.svg'
import Img2 from '../images/search-icon.svg'
import Img3 from '../images/watchlist-icon.svg'
import Img4 from '../images/original-icon.svg'
import Img5 from '../images/movie-icon.svg'
import Img6 from '../images/series-icon.svg'
function Header() {
    const histroy = useHistory()
    const dispatch = useDispatch()
    const userName = useSelector(selectUserName)
    const userPhoto = useSelector(selectUserPhoto)
    useEffect(() => {
        auth.onAuthStateChanged(async (user) => {
            if (user) {
                dispatch(setUserLogin({
                    name: user.displayName,
                    email: user.email,
                    photo: user.photoURL
                }))
                histroy.push("/")
            }
        })
    }, [])
    const signIn = () => {
        auth.signInWithPopup(provider)
            .then((result) => {
                let user = result.user
                dispatch(setUserLogin({
                    name: user.displayName,
                    email: user.email,
                    photo: user.photoURL
                }))
            })
    }
    const signOut = () => {
        auth.signOut()
            .then(() => {
                dispatch(setSignOut());
                histroy.push("/login")
            })
    }
    return (
        <Nav>
            <Logo src={logo} />
            {
                !userName ? (
                    <LoginContainer>
                        <Login onClick={signIn}>Login</Login>
                    </LoginContainer>
                ) :
                    <>
                        <NavMenu>
                            <a>
                                <img src={Img1} />
                                <span>HOME</span>
                            </a>
                            <a>
                                <img src={Img2} />
                                <span>SEARCH</span>
                            </a>
                            <a>
                                <img src={Img3} />
                                <span>WATCHLIST</span>
                            </a>
                            <a>
                                <img src={Img4} />
                                <span>ORIGINALS</span>
                            </a>
                            <a>
                                <img src={Img5} />
                                <span>MOVIES</span>
                            </a>
                            <a>
                                <img src={Img6} />
                                <span>SERIES</span>
                            </a>
                        </NavMenu>
                        <UserImg onClick={signOut} src="https://lh3.googleusercontent.com/a-/AOh14GgcyBLYaQ0Iax0nsDwiNwMLZv9wUECo2u78hNnoLg=s96-c" />
                    </>
            }
        </Nav>
    )
}

export default Header

const Nav = styled.nav`   
height: 70px;
background: #090b13;
display: flex;
align-items: center;
padding: 0 36px;
overflow-x: hidden;
`
const Logo = styled.img`
width:80px;
`
const NavMenu = styled.div`
display:flex;
flex:1;
margin-left:25px;
align-items:center;

a{
    display: flex;
    align-items: center;
    padding: 0 12px;
    cursor:pointer;
    img{
        height:20px;
       
    }
    span{
         font-size: 13px;
         letter-spacing: 1.42px;
         position: relative;


        &:after {
         content: "";
         height: 2px;
         background: white;
         position: absolute;
         left: 0px;
         right: 0px;
         bottom: -6px;
         opacity:0;
         transform-origin: left center;
         transition: all 250ms cubic-berizer(0,25,0.46,0.45,0.94) 0s;
         transform:scaleX(0)
        }
    }
    &:hover {
        span:after {
            transform: scaleX(1);
            opacity:1;
        }
    }
}
`
const UserImg = styled.img`
width: 48px;
height: 48px;
border-radius: 50%;
cursor:pointer;

`
const Login = styled.div`
   border: 1px solid #f9f9f9;
   padding: 8px 16px;
   border-radius: 4px;
   letter-spacing: 1.5px;
   text-transform:uppercase;
   background-color: rgba(0,0,0,0.6);
   cursor: pointer;

   &:hover {
     background-color: #f9f9f9;
     color: #000;
     border-color: transparent;
   }
`
const LoginContainer = styled.div`
    flex :1;
    display: flex;
    justify-content: flex-end;
`